import axios from 'axios'
import React, { Component } from 'react'


export default class Login extends Component {

    state={errorMessage:""}
    user={
        email:"",
        password:""
      }
      getDataForm = (e)=>{
          this.user[e.target.name]=e.target.value
      }
      sendData = async (e) =>{
        e.preventDefault()
        let {data} = await axios.post("https://route-egypt-api.herokuapp.com/signin",this.user)
        if (data.message === "success") {
            
            localStorage.setItem("token", data.token);
            window.location.replace(`/movies`);
            // e.target.reset();
        } else {
            this.setState({
                errorMessage:data.message
                
            })
        }
      }

    render() {
        return (
            <>
            <div className="w-75 mx-auto my-5">
                <form onSubmit={this.sendData} action="">
                    <input onChange={this.getDataForm} name="email" type="email" placeholder="Email" className="form-control mt-3" />
                    <input onChange={this.getDataForm} name="password" type="password" placeholder="password" className="form-control mt-3" />
                    {this.state.errorMessage}
                    <button type="submit" className="btn btn-info float-left mt-3">Login</button>
                </form>
            </div>
            </>
        )
    }
}